import React, { useCallback } from 'react';
import '../App.css';
import BirthdayWish from './BirthdayWish';
import Particles from "react-particles";
import { loadFull } from "tsparticles";
import { loadFireflyPreset } from "tsparticles-preset-firefly";
import MyNavBar from "./navbar/NavBar";

function HomePage() {
    const birthdayWishes = [
        {
            name: "James",
            wish: "Happy Birthday! May all your dreams come true!"
        },
        {
            name: "Alice",
            wish: "Wishing you a day filled with joy and laughter!"
        },
        {
            name: "John",
            wish: "May this year bring you lots of happiness and success!"
        },
        {
            name: "Emily",
            wish: "Here's to another year of wonderful adventures and memories!"
        },
        {
            name: "Michael",
            wish: "May your birthday be as special as you are!"
        },
        {
            name: "gg",
            wish: "Here's to another year of wonderful adventures and memories!"
        },
        {
            name: "ss",
            wish: "Here's to another year of wonderful adventures and memories!"
        },
        {
            name: "cc",
            wish: "Here's to another year of wonderful adventures and memories!"
        },
        {
            name: "aa",
            wish: "Here's to another year of wonderful adventures and memories!"
        },
        {
            name: "xx",
            wish: "Here's to another year of wonderful adventures and memories!"
        }
    ];
    const particlesInit = useCallback(async (engine) => {
        await loadFireflyPreset(engine);
    }, []);

    const particlesConfig = {
        preset: "firefly",
    }

    const count = birthdayWishes.length;
    let heading = "0 Birthday Wishe";
    if (count > 0) {
        const noun = count > 1 ? 'Birthday Wishes' : 'Birthday Wish';
        heading = count + ' ' + noun;
    }

    const divStyle = {
        backgroundImage: "url('/assets/hollow-knight-god-home.gif')",
        backgroundSize: "cover",
        backgroundPosition: "center",
        color: "white",
        padding: "30px",
        textAlign: "center",
        fontSize: "50px",
        height: "100vh",
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
        zIndex: 0,
        backgroundColor: "transparent",
        position: "relative",
    };
    return (
        <div className="App" style={{ position: "relative" }}>
            <div style={divStyle}>
                Happy Birthday Tee Yap!
            </div>
            <MyNavBar />
            <Particles height="100vh" width="100vw" options={particlesConfig} init={particlesInit} />

            <main>
                <h1 style={{ color: 'white' }}> {heading}</h1>
                <section style={{ display: "flex", flexWrap: "wrap", justifyContent: "center" }}>
                    {birthdayWishes.map((name) => (
                        <BirthdayWish key={name} birthday={name} />
                    ))}
                </section>
            </main>
        </div>
    );
}

export default HomePage;